  text-decoration-color: #aa2424;
}

/* Base Styling */
.h-phone-btn svg {
  width: 30px;
  height: 30px;
}

.h-phone-btn svg path {
  fill: #aa2424;
}

.h-phone-btn svg path:nth-child(1) {
  fill: none;
  stroke-width: 4px;
  stroke: #aa2424;
}

.h-phone-btn svg path:nth-child(2) {
  animation: header-phone 1s infinite cubic-bezier(0.1, 0.57, 0.5, 1);
}

.h-phone-btn svg path:nth-child(3) {
  animation: header-phone2 1s infinite cubic-bezier(0.1, 0.57, 0.5, 1);
}

/* Animate phone */
@keyframes header-phone {
  0%, 30% {
    opacity: 0;
    transform: translate3d(-20px, 20px, 0);
  }
  80% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  100% {
    opacity: 0;
  }
}
@keyframes header-phone2 {
  0% {
    opacity: 0;
    transform: translate3d(-20px, 20px, 0);
  }
  70%, 90% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  100% {
    opacity: 0;
  }
}
